.note {
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px rgb(179, 178, 178);
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}
.note h1 {
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p {
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.note button {
  position: relative;
  float: right;
  margin-right: 10px;
  color: #f5ba13;
  border-radius: 50%;
  padding: 5px;
  border: none;
  width: 36px;
  height: 36px;
  cursor: pointer;
  outline: none;
}
/* z-index: 1;
  padding: 5px; */
/* TODO: use z-index for positioning elements*/

button:hover {
  background-color: rgb(213, 213, 213);
}

@media (max-width: 768px) {
  .note {
    width: 200px;
    margin-left: 120px;
  }

  .note h1 {
    font-size: 0.8em;
  }

  .note p {
    font-size: 0.8em;
  }

  .note button {
    padding: 3px;
    width: 30px;
    height: 30px;
  }
}
